body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  height: 100%;
}


@font-face {
  font-family: medievalIntro;
  src: url(/static/media/vinque.214ca8e7.ttf);
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}



.loginView{
  background-image: url(/static/media/splashScreen.bc23871a.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
}

.loginContainer{
  padding-top: 25vh;
}

.loginForm  {
  max-width: 500px;
  border: white 1px solid;
  border-radius: 5px;
  box-shadow: 0 4px 10px 4px;
  background-color: rgba(58, 58, 74, 0.6);
  margin: 0 auto;
}

.loginForm h1{
  padding-top: 20px;
  padding-left: 120px;
}

.gameName{
  font-family: medievalIntro;
}

.btnLogin, .btnRegistar{
  max-width: 300px;
}

.btnlv{
  text-align: center;
  margin-bottom: 2rem;
}

.btnlv button {
  margin: 0 .2rem;
  font-size: 1rem;
  height: 4rem;
  width: 8rem;
}

.loginImages{
  max-width: 25px;
  max-height: 25px;
}

.modalHead{
  background-color: white;
  width: 50vw;
  height: 50vh;
}

.backdrop{
  width: 300px;
}

#city{
  background-image: url(/static/media/citylvl0.e9c26d0d.png);
  height: 500px;
  background-repeat: no-repeat;
  width: 680px;
  margin: 0 auto;
}

#cityView{
  background-color: burlywood;
}

#header {
  height: 40px;
  background-color: #bfbfbf;
}

.resource{
  float: right;
  margin: 0rem 1rem;
  border: #b3b3b3 solid 2px;
  padding: 0.2rem;
}

.resource img{
  margin: 0rem .2rem;
}

.btnBuilding {
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  position: relative;
  cursor: pointer;
}

.error{
  color: red;
}

.fake-link{
  cursor:pointer;
  color:#40a9ff;
}

.fake-link:hover{
  text-decoration:underline;
}
